<template>
  <div>
    <h1></h1>
    <!-- itemsitemitem_id, parent, name -->
    <el-table
      :data="items"
      border
      stripe
      :header-cell-style="rowClass"
      :cell-style="cellStyle"
    >
      <el-table-column prop="_id" label="ID" width="230px"></el-table-column>
      <!-- <el-table-column prop="parent" label=""></el-table-column> -->
      <el-table-column prop="parent.name" label=""></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template v-slot="scope">
          <!-- {{scope.row}} -->
          <el-button
            type="text"
            size="small"
            @click="$router.push(`/categories/edit/${scope.row._id}`)"
            ></el-button
          >
          <el-button type="text" size="small" @click="remove(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get("rest/categories");
      this.items = res.data;
      console.log("this.items is ", this.items);
    },
    async remove(row) {
      // $confirm element
      await this.$confirm(`"${row.name}"`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          const res = await this.$http.delete(`rest/categories/${row._id}`);
          res;
          this.$message({
            type: "success",
            message: "!",
          });
          this.fetch(); // 
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    rowClass() {
      return "background: #cccccc; text-align: center; color: #000000; font-weight: 600;";
    },
    cellStyle() {
      return "background: #ffffff; text-align: center;";
    }
  },
  created() {
    this.fetch();
  },
};
</script>
