<template>
  <div class="about">
    <h1>{{ id ? "" : "" }}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <!-- model  _id, parent, name-->
        <el-select
          placeholder=""
          v-model="model.parent"
          filterable
          style="width: 100%"
        >
          <!-- parentsitemitem_idparent, namename -->
          <el-option
            v-for="item of parents"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <!-- inputmodel -->
        <el-input placeholder="" v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit" round></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {},
  },
  data() {
    return {
      model: {},
      parents: [],
    };
  },
  methods: {
    async save() {
      // this.$http.post('categories', this.model).then()
      let res;
      if (this.id) {
        console.log(
          "this.id and $route.params.id ",
          this.id,
          this.$route.params.id
        );
        res = await this.$http.put(`rest/categories/${this.id}`, this.model);
        res;
      } else {
        // this.modelaxiosreq.bodyexpressmongodb
        res = await this.$http.post("rest/categories", this.model);
        res;
      }
      this.$router.push("/categories/list");
      this.$message({
        type: "success",
        message: "",
      });
      // 
      // const url = this.id ? `rest/categories/${this.id}` : "rest/categories";
      // const method = this.id ? "put" : "post";
      // await this.$http[method](url, this.model);
      // this.model = {};
      // // this.$router.go(-1)
      // this.$router.push("/categories/list");
      // this.$message.success("");
    },
    // 
    async fetch() {
      // populate_idnameidparentparent
      const res = await this.$http.get(`rest/categories/${this.id}`);
      console.log("res is ", res);
      this.model = res.data;
      console.log("this.model is ", this.model);
    },
    // 
    async fetchParents() {
      const res = await this.$http.get(`rest/categories`);
      // this.parents = res.data
      // 
      for (var i = 0; i < res.data.length; i++) {
        if (!res.data[i].parent) {
          this.parents.push(res.data[i]);
        }
      }
      console.log("this.parents is ", this.parents);
    },
  },
  created() {
    this.fetchParents();
    // idmongodb
    this.id && this.fetch();
  },
};
</script>

